import { DeepPartial } from '../types/utility.types';
import {
  IdentityJourneyConfiguration,
  JourneyConfigKeyValuePair,
  JourneyConfigValueType,
} from '../types/journey.types';

export const createJourneyConfiguration = <T extends IdentityJourneyConfiguration>(
  configDefaults: T,
  configOverrides?: DeepPartial<T>,
) =>
  Object.freeze(
    configOverrides
      ? Object.entries(configOverrides).reduce(
          (config: T, [prop, configValues]: [string, JourneyConfigKeyValuePair | undefined]) => ({
            ...config,
            ...(prop in config && configValues
              ? { [prop]: filterUndefinedPropertyValues(config[prop], configValues) }
              : {}),
          }),
          configDefaults,
        )
      : configDefaults,
  );

const filterUndefinedPropertyValues = (
  defaultConfig: JourneyConfigKeyValuePair,
  configOverrideValues: JourneyConfigKeyValuePair,
) =>
  Object.freeze(
    Object.entries(configOverrideValues).filter(undefinedFilter).reduce(entriesToObjectTransformReducer, defaultConfig),
  );

const entriesToObjectTransformReducer = (
  accum: JourneyConfigKeyValuePair,
  [prop, val]: [string, JourneyConfigValueType],
) => ({
  ...accum,
  [prop]: val,
});

const undefinedFilter = (kayValTuple: [string, JourneyConfigValueType]) => kayValTuple[1] !== undefined;
